[class^="rm-text"] {
  font-family: Arial, Helvetica, sans-serif
}

.rm-emoji {
  display: inline-block;
  vertical-align: baseline;
  margin-right: 5px;
}

.rm-text-h1 {
  font-size: 55px;
  font-weight: bold;
}

.rm-text-h2 {
  font-size: 39px;
  font-weight: bold;
}

.rm-text-h3 {
  font-size: 31px;
  font-weight: bold;
}

.rm-text-3 .rm-emoji{
  height: 22px;
  width: 22px;
}

.rm-text-1 {
  font-size: 39px;
}

.rm-text-2 {
  font-size: 31px;
}

.rm-text-3 {
  font-size: 22px;
}

.rm-text-4 {
  font-size: 18px;
}

.rm-text-5 {
  font-size: 15px;
}

.rm-text-6 {
  font-size: 13px;
}

@media only screen and (max-width: 700px) {
  .rm-text-h1 {
    font-size: 9vw;
    font-weight: bold;
  }
  
  .rm-text-h2 {
    font-size: 7vw;
    font-weight: bold;
  }
  
  .rm-text-h3 {
    font-size: 5vw;
    font-weight: bold;
  }
  
  .rm-text-1 {
    font-size: 5vw;
  }
  
  .rm-text-2 {
    font-size: 20px;
  }

  .rm-text-3 {
    font-size: 18px;
  }
}

.rm-typed-anime {
  overflow: hidden;
  border-right: 2px solid black;
  white-space: nowrap;
  opacity: 0;
  margin: 0 auto;
  animation: 
    typing 1.5s steps(18, end) forwards,
    blink-caret .75s step-end infinite;
}

.rm-link {
  text-decoration: underline;
  cursor: pointer;
}

/* The typing effect */
@keyframes typing {
  from { opacity: 1; width: 0% }
  to { opacity: 1; width: 100%;}
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: black; }
}

@keyframes increase-width {
  from { max-width: 0 }
  to { max-width: 100%;}
}